
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

export default {
directives: {
TransferDom
},
components: {
Popup,
Alert,
Confirm
},
data() {
return {
showType: false,
disabled:true,
cardType:'身份证',
cardTypeId:1,
cardNum:'',
showAlert:false,
showCofirmTitle:false,
typeList:[
{
id:1,
type:'身份证'
},
{
id:1,
type:'港澳居民身份证'
},
{
id:1,
type:'台胞证'
},
{
id:1,
type:'护照'
},
]
};
},
methods:{
showTypePage(){
this.showType = true;
},

isCardNo(card){
let  reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
if(reg.test(card) === false){
this.showAlert = true;
return  false;
}else{
this.disabled = false;
return true;
}
},
changeType(item){
this.cardType = item.type;
this.cardTypeId = item.id;
setTimeout(() =>{
this.cardType = item.type;
this.cardTypeId = item.id;
this.showType = false;
},500)
},
checkType(){
this.isCardNo(this.cardNum)
},
checkIsMember(){
let params = {
cardType:this.cardTypeId,
cardNo:this.cardNum
}
this.$axios.get('/api/player/get_player',{params}).then( res =>{
console.log(res)
if(res.data.code === 1){
this.showCofirmTitle = true;
}else{
sessionStorage.setItem('chessInfo',JSON.stringify(res.data.data))
this.$router.push({name:'addPlayerInfo'})
}
})
},
onConfirm(){
alert('功能开发中')
},
onCancel(){

}
}
};



